import PropTypes from 'prop-types';

function TransactionHistory({ data }) {
    return <div>
        <table class="transaction-history">
  <thead>
    <tr>
      <th>Type</th>
      <th>Amount</th>
      <th>Currency</th>
    </tr>
</thead>
            <tbody>
                {data.map(item => (
                    <tr  key={item.id}>
      <td>{item.type}</td>
      <td>{item.amount}</td>
      <td>{item.currency}</td>
    </tr>
                ))}
  </tbody>
</table>
    </div>
}

TransactionHistory.propTypes = {
    items: PropTypes.arrayOf(PropTypes.shape)
}

export default TransactionHistory